  .bunner-button {
    color: white;
    display: inline-block;
    border-radius: 10px;
    padding: 15px 30px;
    border: 2px solid white;
    transition: 0.5s;
    font-family: CalibriLight;
    text-transform: uppercase;
    font-size: 34px;
    line-height: 34px;
    margin-top: 20px;
    letter-spacing: 0px;
    &:hover {
      box-shadow: 0 11px 15px rgba(0,0,0,0.2);
      padding: 15px 60px;
      background-color: $brand-primary; }
    @media (min-width: 1800px) {
      font-size: 40px;
      line-height: 40px;
      padding: 20px 40px;
      margin-top: 25px;
      &:hover {
        padding: 20px 60px; } }
    @media (max-width: 800px) {
      font-size: 30px;
      line-height: 30px; } } }
