@charset "UTF-8";
@font-face {
  font-family: Calibri;
  font-display: swap;
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/Calibri/Calibri.ttf");
}

@font-face {
  font-family: CalibriLight;
  font-display: swap;
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/Calibri/Calibri-Light.ttf");
}

@font-face {
  font-family: CalibriBold;
  font-display: swap;
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/Calibri/Calibri-Bold.ttf");
}

@font-face {
  font-family: NewChannel;
  font-display: swap;
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/New_Channel.ttf");
}

/*РЕЗЕТ*/
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-weight: 200;
  list-style-type: none;
  outline: none;
  outline: 0;
  outline-offset: 0;
  box-sizing: border-box;
  border: none;
  width: auto;
  height: auto;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  outline: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
  overflow-x: hidden;
  width: 100%;
  text-align: center;
}

ol,
ul {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
textarea,
select,
button {
  border: none;
  outline: none;
  outline-offset: none;
  background: none;
  border-radius: 0;
}

*:active,
*:hover,
*:focus {
  outline: 0;
  outline-offset: none;
}

* {
  font-size: 18px;
  color: #000;
  font-family: CalibriLight;
  line-height: 22px;
}

@media (min-width: 1800px) {
  * {
    font-size: 24px;
    line-height: 28px;
  }
}

body {
  background-color: #ECECEC;
  text-align: center;
  position: relative;
  overflow-x: hidden;
}

@media (min-width: 1800px) {
  body {
    font-size: 22px;
  }
}

.page-container {
  display: inline-block;
  width: 1300px;
  margin: 0 auto;
  text-align: center;
}

@media (min-width: 1800px) {
  .page-container {
    width: 1700px;
  }
}

@media (max-width: 1300px) {
  .page-container {
    width: 1000px;
  }
}

@media (max-width: 1024px) {
  .page-container {
    width: 95%;
  }
}

#overlay {
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 10;
  display: none;
  user-select: none;
  opacity: 0;
}

@keyframes pulse {
  from {
    transform: scale(1.05);
  }
  50% {
    transform: scale(1);
  }
  to {
    transform: scale(1.05);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#preloader {
  background-color: white;
  position: fixed;
  z-index: 20;
  width: 150%;
  height: 150%;
  left: -25%;
  top: -25%;
}

#preloader img {
  width: 150px;
  height: 150px;
  position: fixed;
  left: 50%;
  margin-left: -75px;
  top: 50%;
  margin-top: -75px;
  animation: pulse  3s infinite ease-in-out, fade-in 1s;
}

#agreement-popup {
  position: fixed;
  left: 30px;
  bottom: 30px;
  background-color: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  z-index: 11;
  padding: 20px;
  display: none;
  opacity: 0;
}

@media (min-width: 1800px) {
  #agreement-popup {
    left: 60px;
    bottom: 60px;
  }
}

@media (max-width: 550px) {
  #agreement-popup {
    left: 0;
    bottom: 0;
    width: 100%;
  }
}

#agreement-popup p {
  text-align: left;
  display: inline-block;
  width: 350px;
  vertical-align: middle;
}

@media (min-width: 1800px) {
  #agreement-popup p {
    width: 400px;
  }
}

@media (max-width: 550px) {
  #agreement-popup p {
    width: 65%;
  }
}

#agreement-popup p a {
  color: #C1272D;
  transition: 0.5s;
}

#agreement-popup p a:hover {
  color: #FBB03B;
}

#agreement-popup div.button {
  with: 50px;
  display: inline-block;
  margin-left: 15px;
  vertical-align: middle;
}

@media (min-width: 1800px) {
  #agreement-popup div.button {
    width: 80px;
  }
}

.background-dish-container {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  left: 0;
  top: 0;
}

.background-dish {
  position: absolute;
  background-repeat: repeat-y;
  background-size: contain;
  background-position: center;
  height: 100%;
  width: 180px;
  overflow: hidden;
  top: 0;
  z-index: -1;
  opacity: 0.7;
}

@media (min-width: 1800px) {
  .background-dish {
    width: 200px;
  }
}

.background-dish-left {
  left: -30px;
  background-image: url(/img/bg-dish.png);
}

@media (min-width: 1800px) {
  .background-dish-left {
    left: -40px;
  }
}

.background-dish-right {
  right: -30px;
  background-image: url(/img/bg-dish-2.png);
}

@media (min-width: 1800px) {
  .background-dish-right {
    right: -40px;
  }
}

#cart_add-icon {
  position: fixed;
  left: 0;
  top: 0;
  width: 30px;
  height: 30px;
  z-index: 6;
  display: none;
}

#cart_add-icon img {
  width: 100%;
  height: 100%;
}

.movingCartAnimation {
  transform: rotate(5deg);
}

.button {
  color: white;
  display: inline-block;
  border-radius: 10px;
  padding: 10px 20px;
  background-color: #C1272D;
  transition: 0.5s;
  font-family: Calibri;
  vertical-align: middle;
  cursor: pointer;
}

@media (min-width: 1800px) {
  .button {
    font-size: 24px;
  }
}

.button p {
  font-family: Calibri;
  vertical-align: middle;
  color: white;
  display: inline-block;
}

@media (min-width: 1800px) {
  .button p {
    font-size: 24px;
  }
}

.button img {
  width: 20px;
  heihgt: 20px;
  vertical-align: middle;
  display: inline-block;
}

.button:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  transform: scale(1.1);
}

.label-new {
  position: absolute;
  right: 0;
  top: 10%;
  background-color: #FBB03B;
  color: white;
  padding: 10px 20px;
  font-family: Calibri;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.header-separator {
  font-size: 30px;
  line-height: 30px;
  padding-bottom: 20px;
  text-align: left;
}

@media (min-width: 1800px) {
  .header-separator {
    font-size: 40px;
    line-height: 40px;
    padding-bottom: 25px;
  }
}

.switch-btn {
  display: block;
  width: 60px;
  height: 30px;
  border-radius: 15px;
  background: lightgray;
  z-index: 0;
  margin: 0;
  margin-top: 10px;
  padding: 0;
  border: none;
  cursor: pointer;
  position: relative;
  transition-duration: 300ms;
}

.switch-btn p.switch-btn-notes {
  position: absolute;
  color: white;
  font-size: 12px;
  line-height: 12px;
}

.switch-btn p.switch-btn-notes.true {
  left: 12px;
  top: 8px;
}

.switch-btn p.switch-btn-notes.false {
  left: 32px;
  top: 9px;
}

.switch-btn::after {
  content: "";
  height: 24px;
  width: 24px;
  border-radius: 18px;
  background: #fff;
  top: 3px;
  left: 3px;
  transition-duration: 300ms;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  position: absolute;
  z-index: 1;
}

.switch-on {
  background: #FBB03B;
}

.switch-on::after {
  left: 32px;
  background: white;
}

/* !!! МЕНЮ ДЕСКТОП !!! */
#fixed-menu {
  width: 100%;
  background-color: white;
  position: fixed;
  top: -200px;
  left: 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  display: block;
  z-index: 5;
  user-select: none;
  text-align: center;
  transition: 0.5s;
}

@media (max-width: 1024px) {
  #fixed-menu {
    display: none;
  }
}

#fixed-menu .line-1 {
  background-color: #FBB03B;
  height: 50px;
  padding-top: 3px;
}

@media (min-width: 1800px) {
  #fixed-menu .line-1 {
    height: 65px;
    padding-top: 5px;
  }
}

#fixed-menu .line-1 * {
  color: white;
}

#fixed-menu .line-1 .lang-controller {
  float: left;
  margin-left: 100px;
  margin-top: 10px;
}

@media (min-width: 1800px) {
  #fixed-menu .line-1 .lang-controller {
    margin-left: 120px;
    margin-top: 15px;
  }
}

#fixed-menu .line-1 .lang-controller * {
  display: inline-block;
}

#fixed-menu .line-1 .lang-controller p {
  margin: 0 5px;
  cursor: default;
}

#fixed-menu .line-1 .lang-controller .active {
  font-family: Calibri;
  font-weight: bold;
  color: #C1272D;
}

#fixed-menu .line-1 .lang-controller a {
  transition: 0.3s;
  border-bottom: 1px solid #FBB03B;
}

#fixed-menu .line-1 .lang-controller a:hover {
  border-bottom: 1px solid white;
}

#fixed-menu .line-1 .cart {
  margin-left: 20px;
}

#fixed-menu .line-1 .cart, #fixed-menu .line-1 .phone {
  float: right;
  display: inline-block;
  padding: 10px;
  vertical-align: middle;
  text-align: right;
}

@media (min-width: 1800px) {
  #fixed-menu .line-1 .cart, #fixed-menu .line-1 .phone {
    padding: 12px 20px;
  }
}

#fixed-menu .line-1 .cart p, #fixed-menu .line-1 .phone p {
  display: inline-block;
  vertical-align: middle;
  font-family: Calibri;
  color: white;
}

@media (min-width: 1800px) {
  #fixed-menu .line-1 .cart p, #fixed-menu .line-1 .phone p {
    font-size: 24px;
  }
}

#fixed-menu .line-1 .cart p b, #fixed-menu .line-1 .phone p b {
  font-family: Calibri;
  color: white;
}

#fixed-menu .line-1 .cart.disabled, #fixed-menu .line-1 .phone.disabled {
  background-color: lightgray;
}

#fixed-menu .line-2 {
  background-color: white;
  padding: 0;
  vertical-align: middle;
}

#fixed-menu .line-2 .page-container {
  position: relative;
}

#fixed-menu .line-2 nav {
  display: inline-block;
  vertical-align: middle;
}

#fixed-menu .line-2 nav .menu-list {
  text-align: center;
}

#fixed-menu .line-2 nav .menu-list li {
  display: inline-block;
}

#fixed-menu .line-2 nav .menu-list li a {
  margin: 0 5px;
  display: inline-block;
  vertical-align: middle;
  padding: 15px;
  transition: 0.5s;
  cursor: pointer;
}

#fixed-menu .line-2 nav .menu-list li a p {
  display: inline-block;
  font-family: Calibri;
  vertical-align: middle;
  margin-left: 5px;
  font-size: 18px;
  transition: 0.5s;
}

@media (min-width: 1800px) {
  #fixed-menu .line-2 nav .menu-list li a p {
    font-size: 22px;
  }
}

#fixed-menu .line-2 nav .menu-list li a img {
  width: 25px;
  height: 25px;
  vertical-align: middle;
}

@media (min-width: 1800px) {
  #fixed-menu .line-2 nav .menu-list li a img {
    width: 27px;
    height: 27px;
  }
}

@media (min-width: 1800px) {
  #fixed-menu .line-2 nav .menu-list li a {
    margin: 0 10px;
  }
}

#fixed-menu .line-2 nav .menu-list li:hover > a > p {
  color: #C1272D;
}

#fixed-menu .line-2 nav .menu-list li.drop-down {
  position: relative;
}

#fixed-menu .line-2 nav .menu-list li.drop-down img {
  width: 10px;
  margin-left: 5px;
  margin-bottom: -3px;
}

#fixed-menu .line-2 nav .menu-list li.drop-down > ul {
  display: block;
  width: 250px;
  position: absolute;
  z-index: 4;
  background-color: white;
  box-shadow: 0px 6px 9px 3px rgba(0, 0, 0, 0.3);
  text-align: left;
  display: none;
}

#fixed-menu .line-2 nav .menu-list li.drop-down > ul li {
  display: block;
  border-bottom: 1px solid lightgray;
}

#fixed-menu .line-2 nav .menu-list li.drop-down:hover > ul {
  display: block;
}

#fixed-menu .line-2 nav .logo {
  text-align: left;
  background-color: white;
  border-radius: 100px;
  position: absolute;
  bottom: 10px;
  left: 0;
  height: 80px;
  width: 80px;
  transition: 0.5s;
}

@media (min-width: 1800px) {
  #fixed-menu .line-2 nav .logo {
    width: 100px;
    height: 100px;
  }
}

#fixed-menu .line-2 nav .logo img {
  width: 80px;
  height: 80px;
}

@media (min-width: 1800px) {
  #fixed-menu .line-2 nav .logo img {
    width: 100px;
    height: 100px;
  }
}

#fixed-menu .line-2 nav .logo:hover {
  transform: scale(1.1);
  background-color: #C1272D;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

/* !!! МЕНЮ MOBILE !!! */
