
#items {
  margin-top: 0px;
  margin-bottom: 150px;
  position: relative;
  z-index: 1;
  .category {
    text-align: left;
    padding-top: 120px;
    .items-container {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-column-gap: 10px;
      grid-row-gap: 20px;
      @media (min-width: 1800px) {
        grid-column-gap: 20px;
        grid-row-gap: 30px; }
      @media (max-width: 1300px) {
        grid-template-columns: 1fr 1fr 1fr; }
      @media (max-width: 1024px) {
        grid-template-columns: 1fr 1fr; }
      @media (max-width: 750px) {
        grid-template-columns: 1fr; }

      .item {
        display: inline-block;
        overflow: hidden;
        cursor: pointer;
        transition: 0.5s;
        background-color: white;
        position: relative;
        padding-bottom: 80px;
        border-radius: 15px;
        box-shadow: 0 11px 15px rgba(0,0,0,0.2);
        @media (min-width: 1800px) {
          padding-bottom: 90px; }
        .img-container {
          width: 100%;
          height: 300px;
          position: relative;
          overflow: hidden;
          @media (min-width: 1800px) {
            height: 400px; }
          img {
            position: absolute;
            object-fit: cover;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            transition: 0.5s; } }

        &:hover {
          box-shadow: 0 0 5px rgba(0,0,0,0.5);
          .img-container {
            img {
              transform: scale(1.1); } } }
        .text-container {
          padding: 15px;
          @media (min-width: 1800px) {
            padding: 20px; }
          h3 {
            font-size: 28px;
            line-height: 28px;
            margin-bottom: 15px;
            @media (min-width: 1800px) {
              font-size: 34px;
              line-height: 34px;
              margin-bottom: 20px; } }
          strong {
            padding-bottom: 10px;
            display: block;
            @media (min-width: 1800px) {
              padding-bottom: 20px; }
            img {
              width: 35px;
              padding-right: 10px;
              display: inline;
              vertical-align: middle; }
            p {
              font-size: 18px;
              line-height: 18px;
              font-weight: bold;
              color: $brand-primary;
              display: inline;
              vertical-align: middle;
              @media (min-width: 1800px) {
                font-size: 22px;
                line-height: 22px; } } }
