        .price-container {
          padding: 0 15px;
          width: 100%;
          ul {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(50px, 0.5fr));
            grid-gap: 5px;
            align-items: end;
            .price-elem {
              cursor: pointer;
              text-align: center;
              transition: 0.2s;
              padding: 10px 0px;
              position: relative;
              border: 1px solid lightgray;
              border-radius: 15px;
              @media (min-width: 1800px) {
                padding: 15px 0px; }
              &.clickable:hover {
                box-shadow: 0 0 5px rgba(0,0,0,0.2);
                border: 1px solid lightgreen; }
              &.clickable.active {
                border: 1px solid green; }
              &.lone {
                border: none;
                width: 100px;
                position: absolute;
                bottom: 15px;
                left: 30px; }

              div {
                p {
                  display: inline-block;
                  margin: 0;
                  padding: 0; }

                .price {
                  font-weight: bold;
                  font-size: 22px;
                  line-height: 22px;
                  @media (min-width: 1800px) {
                    font-size: 26px;
                    line-height: 26px; } }
                .currency {
                  font-weight: bold; }
                &.c1 {
                  position: absolute;
                  right: 50%;
                  margin-right: -20px;
                  top: -5px;
                  @media (min-width: 1800px) {
                    margin-right: -25px; }
                  .old-price,.currency {
                    font-size: 14px;
                    color: gray;
                    text-decoration: line-through;
                    font-weight: normal;
                    @media (min-width: 1800px) {
                      font-size: 18px; } } } } } } }




        .cart {
          position: absolute;
          bottom: 25px;
          right: 10px;
          img {
            margin-top: -5px; } } } } } }
